	import { post,tokenpost} from '@/api/user.js';
	import { api } from '@/config/common.js';
export default {
		data() {
		return {
			title: '分享',
			userId:0,
			shareText: '',
			href: '',
			image: '',
			shareType: 1,
			providerList: [],
			dataList:{}
		};
	},
	computed: {
		isDisableButton() {
			return function(item) {
				if (this.shareType === 0 && item.id === 'qq') {
					return true;
				}
				if (this.shareType === 5 && item.name !== '分享到微信好友') {
					return true;
				}
				return false;
			};
		}
	},
	onShareAppMessage() {
		return {
			title: this.shareText,
			path: '/pages/index/index?promoterid='+uni.getStorageSync('userInfo').userId,
			imageUrl: this.image,
			complete:this.shareSuccess
		};
	},
	onUnload: function() {
	},
	onLoad: function() {
		this.loadData();
		this.userId= uni.getStorageSync('userInfo').userId;
		console.log(this.userId);
		uni.getProvider({
			service: 'share',
			success: e => {
				console.log('success', e);
				let data = [];
				for (let i = 0; i < e.provider.length; i++) {
					switch (e.provider[i]) {
						case 'weixin':
							data.push({
								name: '分享到微信好友',
								id: 'weixin',
								sort: 0
							});
							data.push({
								name: '分享到微信朋友圈',
								id: 'weixin',
								type: 'WXSenceTimeline',
								sort: 1
							});
							break;
						case 'sinaweibo':
							data.push({
								name: '分享到新浪微博',
								id: 'sinaweibo',
								sort: 2
							});
							break;
						case 'qq':
							data.push({
								name: '分享到QQ',
								id: 'qq',
								sort: 3
							});
							break;
						default:
							break;
					}
				}
				this.providerList = data.sort((x, y) => {
					return x.sort - y.sort;
				});
			},
			fail: e => {
				console.log('获取分享通道失败', e);
				uni.showModal({
					content: '获取分享通道失败',
					showCancel: false
				});
			}
		});
	},
	onShow() {
		//this.loadData();
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			});
		},
		handleMyshare(){
			uni.navigateTo({
				url:'./myshare'
			})
		},
		loadData(){
			tokenpost(api.GetCurrentActivity).then(res => {
				console.log(res);
				if (res.status == 200 && res.data.returnCode == '0000') {
				  this.shareText = res.data.data.activityexplain;
				  if(res.data.data.imgurl!=''){
					  this.image = api.baseUrl+res.data.data.imgurl;
				  }
				  
				} else {
					this.$api.msg(res.data.returnMessage) 
				}
				this.loading =false;
			}).catch(error => {
				this.loading =false;
				this.$api.msg('请求失败fai') 
			})
		},
		shareSuccess(){
			console.log('成功2222222');
		}
	}
};
